import React, { useState, useEffect } from "react";
import Nav from "../components/nav"
import { FaHeadphones, FaMicrophone, FaLaptop} from "react-icons/fa";
import { Helmet } from "react-helmet"

export default () => {
    const [projHTML, setProjHTML] = useState(0);

    useEffect(() => {
        let didCancel = false;
        fetch("/projects.json")
        .then(res => {
            if (!res.ok) throw Error(res.statusText);
            return res.json();
          })
        .then(data => data.sort((a, b) => {
            let dateA = new Date(a.date).getTime();
            let dateB = new Date(b.date).getTime();
            return dateB - dateA;
        }))
        .then(data => data.map(project => {
            var dateStringArr = project.date.split(' ');
            var dateString = `${dateStringArr[0]} ${dateStringArr[2]}`
            return (<div key={project.id} className="smallProjWrap">
                <span>{project.type === "Music" ? <FaHeadphones /> : (project.type === "Software" ? <FaLaptop /> : <FaMicrophone />)} <b>{project.type}</b></span>
                <a href={project.link}
                target="_blank"
                rel="noopener noreferrer" >
                    <div className="smallProj">
                        <p>{project.name.toUpperCase()}</p>
                        <p>{project.description}</p>
                    </div>
                </a>
                <span>{dateString}</span>
            </div>);
        }))
        .then((html) => {
            if (!didCancel) {
                setProjHTML(html);
            }
        })
        .catch(console.log);
        return () => {
            didCancel = true;
        };
    }, [])
    
    return (<Nav>
        <Helmet>
            <title>Projects - Eashwar Mohan</title>
            <meta charSet="utf-8" />
            <meta property="og:type" content="website" />
            <meta property="og:title" content="Eashwar Mohan" />
            <meta property="og:description" content="The online portfolio for Eashwar Mohan." />
            <meta property="og:url" content="https://eash.dev" />
            <meta property="og:image" content="https://eash.dev/me.png" />
            <meta name="author" content="Eashwar Mohan" />
            <meta name="description" content="The online portfolio for Eashwar Mohan." />
        </Helmet>
        <p className="about" style={{marginBottom: "4vw", marginTop: "2vw"}}>
            Below is a more comprehensive list of all of the music and software projects I've worked on since 2015. Feel free to reach out to me if you have any questions about any of the projects here!
        </p>
        {projHTML}
    </Nav>)
}